const base_url = "https://api.rawg.io/api/";

//getting date

const getCurrentMonth = () => {
	const month = new Date().getMonth() + 1;
	if (month < 10) {
		return `0${month}`;
	} else {
		return month;
	}
};

const getCurrentDay = () => {
	const day = new Date().getDate();
	if (day < 10) {
		return `0${day}`;
	} else {
		return day;
	}
};
const currentYear = new Date().getFullYear();
const currentMonth = getCurrentMonth();
const currentDay = getCurrentDay();

const currentDate = `${currentYear}-${currentMonth}-${currentDay}`;

const lastYear = `${currentYear - 1}-${currentMonth}-${currentDay}`;
const nextYear = `${currentYear + 1}-${currentMonth}-${currentDay}`;

const popular_games = `games?key=391e86c8376948fbb2ae25c818ef38af&dates=${lastYear},${currentDate}&ordering=-rating&page_size=10`;
const upcoming_games = `games?key=391e86c8376948fbb2ae25c818ef38af&dates=${currentDate},${nextYear}&ordering=-added&page_size=10`;
const newGames = `games?key=391e86c8376948fbb2ae25c818ef38af&dates=${lastYear},${currentDate}&ordering=-released&page_size=10`;


export const popularGamesUrl = () => `${base_url}${popular_games}`;
export const upcomingGamesUrl = () => `${base_url}${upcoming_games}`;
export const newGamesUrl = () => `${base_url}${newGames}`;
export const gamesDetailUrl = (game_id) => `${base_url}games/${game_id}`;
export const gamesScreenshotUrl = (game_id) => `${base_url}games/${game_id}/screenshots`;
export const gameSearchUrl = (game_name) => `${base_url}games?search=${game_name}&page_size=9`;